 /* Blockquote main style */
 .blockquote {
     position: relative;
     font-family: 'Amatic SC', cursive;
     font-family: 'Caveat', cursive;     
     max-width: 420px;
     margin: 50px;
     -webkit-align-self: center;
             align-self: center;
 }
 
 /* Blockquote header */
 .blockquote h1 {
     position: relative; /* for pseudos */
     font-size: 2.8rem;
     font-weight: normal;
     line-height: 1;
     border: 2px solid #fff;
     border: solid 2px;
     border-radius:20px;
     padding: 25px;
 }
 
 /* Blockquote subheader */
 .blockquote h4 {
     position: relative;
     font-size: 1.3rem;
     font-weight: 400;
     line-height: 1.2;
     padding-top: 15px;
     z-index: 1;
     margin-left:150px;
     padding-left:12px;
 }
 
  
 .blockquote h4:first-letter {
   margin-left:-12px;
 }

