#greeting::before {
    content: "";
    -webkit-animation: animate 7s;
            animation: animate 7s;
  }
  
  @-webkit-keyframes animate {
    0% {
      content: "Hello!! ";
      opacity: 1;
    }
    10% {
      opacity: 1;
    }
    25% {
      opacity: 0;
    }
    75% {
      opacity: 1;
      content: "Hola!!! ";
    }
    100% {
      content: "";
    }
  }
  
  @keyframes animate {
    0% {
      content: "Hello!! ";
      opacity: 1;
    }
    10% {
      opacity: 1;
    }
    25% {
      opacity: 0;
    }
    75% {
      opacity: 1;
      content: "Hola!!! ";
    }
    100% {
      content: "";
    }
  }

